.beer_link {
  text-decoration: none;
  cursor: auto;
}

.bears_item {
  display: flex;
  justify-content: center;

  &__content:hover {
    cursor: pointer;
    box-shadow: 0 2px 16px rgba(0, 0, 0, .1);
  }

  &__content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border: solid 2px grey;
    width: 200px;
    min-height: 300px;
    border-radius: 10px;
  }

  &__image {
    padding: 10px;
    max-block-size: 120px;
  }

  &__decoration {
    display: flex;
    align-items: center;

    &__line {
      height: 1px;
      width: 70px;
      background: black;
    }

    &__square {
      margin: 0 10px;
      width: 10px;
      height: 10px;
      background: black;
      transform: rotate(45deg);
    }
  }

  &__name,
  &__tagline {
    text-align: center;
    padding: 10px 5px;
    color: grey
  }
}