@mixin phone {
    @media (max-width: 640px) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: 640px) and (max-width: 1200px) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: 1200px) {
        @content;
    }
}

@mixin pageBreakPoints {
    margin: 0 auto;
    min-width: 320px;
    display: flex;
    flex-direction: column;
    padding: 0 16px;

    @media (min-width: 640px) and (max-width: 1200px) {
        padding: 0 32px;
    }

    @media (min-width: 1200px) {
        padding: 0 32px;
        max-width: 1200px;
    }
}

@mixin phonesBreakPoints {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 16px;

    margin-bottom: 24px;

    @media (min-width: 640px) and (max-width: 1200px) {
        flex-direction: row;
        flex-wrap: wrap;
        margin-bottom: 40px;
    }

    @media (min-width: 1200px) {
        flex-direction: row;
        flex-wrap: wrap;
        margin-bottom: 40px;
    }
}